<template>
  <div class="StockEntryAddDialog">
    <el-dialog
      :visible="visible"
      @update:visible="showDialog"
      title="添加客户"
      :close-on-click-modal="false"
    >
      <div style="display:flex;width:100%;flex-direction:column;margin-bottom:30px;">
        <div style="display:flex;height:40px;background:#eee;  font-weight: 600;" class="box">
          <div>编号</div>
          <div>客户</div>
          <div>合同起始时间</div>
          <div>到期时间</div>
          <div>联系人</div>
          <div>联系方式</div>
          <div>合同</div>
          <div>客户权限</div>
        </div>
        <div style="display:flex;height:80px;border:1px solid #eee;" class="box">
          <div>编号</div>
          <div>编号</div>
          <div>
            <el-date-picker v-model="value1" type="date" placeholder="选择日期" size="mini"></el-date-picker>
          </div>
          <div>
            <el-date-picker v-model="value2" type="date" placeholder="选择日期" size="mini"></el-date-picker>
          </div>
          <div>编号</div>
          <div>15874665499</div>
          <div>编号</div>
          <div>编号</div>
        </div>
      </div>

      <div class="text-center">
        <el-button-submit :is-block="false" @click.native="clickSubmit">提交</el-button-submit>
      </div>
    </el-dialog>
  </div>
</template>

<script>
//import StockEntryModel from "@/project/model/StockEntryModel";
import ClientModel from "@/project/my-model/ClientModel";
//import CategorySelector from "@/components/page-content/enum-selector/CategorySelector";
//import CompanySelector from "@/components/page-content/enum-selector/CompanySelector";	客户下拉框数据
import ElButtonSubmit from "@/components/common/button/ElButtonSubmit";
import Api from "@/assets/api/Api";
import DialogUtil from "@/util/DialogUtil";
import DateTimeRangeInput from "@/components/common/input/DateTimeRangeInput";
import UserHandle from "@/project/user/UserHandle";

export default {
  //name: "StockEntryAddDialog",
  name: "ClientAddDialog",
  components: {
    ElButtonSubmit,
    //CompanySelector,	客户下拉框数据
    DateTimeRangeInput
    //CategorySelector
  },
  props: {
    type: {
      default: 1
    },
    visible: {
      default: false
    },
    editId: {
      default: 0
    }
  },
  data() {
    return {
      //stockEntry: new StockEntryModel(this.type),
      clientEntry: new ClientModel(),
      value1:"",
      value2:""
    };
  },
  mounted() {
    this.reload();
  },
  methods: {
    reload() {},
    showDialog(v) {
      this.$emit("update:visible", v);
    },
    refreshData() {
      if (!this.editId) {
        this.clientEntry = new ClientModel();
        return;
      }
      this.$ajax
        .request(Api.company.getById, {
          id: this.editId
        })
        .then(resp => {
          //this.stockEntry = resp;
          this.clientEntry = resp;
        });
    },

    clickSubmit() {
      console.log("this.clientEntry^^^^^^^^^^^^^^");
      console.log(this.clientEntry);
      //let api = this.editId ? Api.company.edit : Api.company.add;
      let api = Api.company.edit;
      if (!this.editId) {
        //editId==0,执行添加
        this.clientEntry.category = 2;
        this.clientEntry.createUid = UserHandle.getLoginUser().id;
        this.clientEntry.id = null;
        api = Api.company.add;
      } else {
        //编辑修改
        this.clientEntry.updateUid = UserHandle.getLoginUser().id;
      }
      console.log("createUid>>>>>>>>>>>" + this.clientEntry.createUid);
      console.log(UserHandle.getLoginUser());
      //console.log(this.clientEntry.category);
      this.$ajax.request(api, this.clientEntry).then(resp => {
        DialogUtil.toastSuccess(resp);
        this.showDialog(false);
        this.$emit("finish");
      });
    }
  },
  watch: {
    visible(v) {
      if (v) {
        this.refreshData();
      }
    }
  },
  computed: {}
};
</script>

<style lang="less" scoped>
@import (reference) "~style/all.less";
.box div {
  width: 130px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-right: 1px solid #eee;

}
.box div:nth-child(8) {
  border: none;
}
.StockEntryAddDialog {
}
</style>
