<template>
  <div class="CategoryManagerView flex-box flex-column" style="margin-top:20px;">
    <div class="logout">
      <span class="el-icon-warning"></span>
      <span style="font:14px '';margin-left:10px;">客户列表</span>
    </div>
    <div class="flex-box search-card">
      <div class>
        <span>客户:</span>
        <category-type-selector v-model="filterForm.code" :is-filter="true"></category-type-selector>
      </div>
      <div class>
        <span>合同到期时间:</span>
        <category-type-selector v-model="filterForm.code" :is-filter="true"></category-type-selector>
      </div>
      <div>
        <el-input
          placeholder="输入关键词"
          v-model="filterForm.name"
          class="input-with-select"
          width="200px"
          @change="refreshData()"
        >
          <el-button slot="append" icon="el-icon-search" @click.native="refreshData()"></el-button>
        </el-input>
        <div style="position:absolute;right:30px;top:90px;">
          <el-button type="primary" class="el-icon-plus" @click="clickAddButton">添加客户</el-button>
        </div>
      </div>
    </div>
    <div class="flex-grow">
      <!--  -->
      <client-add-dialog :visible.sync="showAddDialog" @finish="refreshData" :edit-id="editId"></client-add-dialog>
    </div>
    <div class="list" style="background:#fff;">
      <el-row style="width:100%;" class="list_nav">
        <el-col :span="2">
          <div class="grid-content">编号</div>
        </el-col>
        <el-col :span="2">
          <div class="grid-content">客户</div>
        </el-col>
        <el-col :span="2">
          <div class="grid-content">合同状态</div>
        </el-col>
        <el-col :span="3">
          <div class="grid-content">合同起始时间</div>
        </el-col>
        <el-col :span="3">
          <div class="grid-content">到期时间</div>
        </el-col>
        <el-col :span="2">
          <div class="grid-content">联系人</div>
        </el-col>
        <el-col :span="3">
          <div class="grid-content">联系方式</div>
        </el-col>
        <el-col :span="2">
          <div class="grid-content">合同</div>
        </el-col>
        <el-col :span="3">
          <div class="grid-content">客户权限</div>
        </el-col>
        <el-col :span="1">
          <div class="grid-content el-icon-s-tools"></div>
        </el-col>
        <el-col :span="1">
          <div class="grid-content"></div>
        </el-col>
      </el-row>
      <el-row style="width:100%;" class="list_box" v-for="de,index in de" v-bind:key="de.id">
        <el-col :span="2">
          <div class="grid-content">1</div>
        </el-col>
        <el-col :span="2">
          <div class="grid-content">xx公司</div>
        </el-col>
        <el-col :span="2">
          <div class="grid-content">未到期</div>
        </el-col>
        <el-col :span="3">
          <div class="grid-content">2019/7/7</div>
        </el-col>
        <el-col :span="3">
          <div class="grid-content">2019/7/7</div>
        </el-col>
        <el-col :span="2">
          <div class="grid-content">联系人</div>
        </el-col>
        <el-col :span="3">
          <div class="grid-content">联系方式</div>
        </el-col>
        <el-col :span="2">
          <div class="grid-content">合同</div>
        </el-col>
        <el-col :span="3">
          <div class="grid-content">客户权限</div>
        </el-col>
        <el-col :span="1">
          <div class="grid-content el-icon-setting"></div>
        </el-col>
        <el-col :span="1">
          <div class="grid-content el-icon-delete" @click="del(index)"></div>
        </el-col>
      </el-row>
    </div>
    <div style="display:flex;align-items:center;margin:10px  auto;">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page.sync="currentPage3"
        :page-size="1"
        layout="total,prev, pager, next, jumper"
        :total="this.de.length"
        background
      ></el-pagination>
    </div>
  </div>
</template>

<script>
import SearchCardLayout from "@/components/layout-component/SearchCardLayout";
import Api from "@/assets/api/Api";
import EcUtil from "@/util/EcUtil";
import DialogUtil from "@/util/DialogUtil";
import ClientModel from "@/project/my-model/ClientModel";
import PaginateModel from "@/project/model/PaginateModel";
import CategoryTypeSelector from "@/components/page-content/enum-selector/CategoryTypeSelector";
import ElButtonMini from "@/components/common/button/ElButtonMini";
import TablePanel from "@/components/layout-component/TablePanel";
import ClientAddDialog from "@/components/page-content/client-manager/dialog/ClientAddDialog";

export default {
  name: "CategoryManagerView",
  components: {
    ClientAddDialog,
    TablePanel,
    ElButtonMini,
    CategoryTypeSelector,
    SearchCardLayout
  },
  props: {},
  data() {
    return {
      loading: true,
      showAddDialog: false,
      filterForm: new ClientModel(),
      data: [],
      paginate: new PaginateModel(this.refreshData),
      selectedRow: [],
      sortingColumn: null,
      editId: 0,
      de: [{}, {}, {}, {}, {}, {}],
      currentPage1: 5,
      num: 100
    };
  },
  mounted() {
    this.reload();
  },
  methods: {
    reload() {
      this.refreshData();
    },
    refreshData() {
      this.loading = true;
      this.$ajax
        .request(Api.company.list, {
          ...EcUtil.getSortField(this.sortingColumn),
          ...this.paginate.getJSON(),
          ...this.filterForm
        })
        .then(resp => {
          this.data = resp.list;
          this.paginate.setPaginate(resp);
        })
        .finally(() => (this.loading = false));
    },
    clickAddButton() {
      this.showAddDialog = true;
    },

    del(index) {
      this.$confirm("确定封停该客户吗?", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
        center: true
      })
        .then(() => {
          this.$message({
            type: "success",
            message: "成功!"
          });
          console.log(index);
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消"
          });
        });
    },
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
    }
  },
  watch: {},
  computed: {}
};
</script>

<style lang="less" scoped>
@import (reference) "~style/all.less";
.logout {
  height: 50px;
  width: 120px;
  border: 1px solid #eee;
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  top: 20px;
  background: #2b3d4f;
  color: #fff;
}
.CategoryManagerView {
}
.list {
  background: #fff;
}
.el-col {
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.list .list_box:nth-child(odd) {
  background: #f9fafc;
}
.list .list_box {
  height: 58px;
  font: 14px "";
  border-bottom: 1px solid #f9f9f9;
  border-top: 1px solid #f9f9f9;
}
.list_nav{
  font: 14px "";
  font-weight: 600;
  background: #f4f5f7;
  border-bottom: 1px solid #f9f9f9;
  border-top: 1px solid #f9f9f9;
}
</style>
